.write{

    padding-top: 50px;
}

.writeImg{

    margin-left: 150px;
    width: 70vw;
    height: 350px;
    border-radius: 10px;
    object-fit: cover;

}

.writeForm{

    justify-content: center;
    margin-left: 150px;
    position: relative;

}

.writFormGroup{

    
    display: flex;
    align-items: center;

}

.writeIcon{

    width: 25px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    color: gray;
    cursor: pointer;
}

.writeInput{

    font-size: 30px;
    border: none;
    padding: 20px;
    width: 70vw;
}

.writeInput:focus{

    outline: none;
}

.writeText{
    font-size: 20px;
    height:100vh ;
}

.writeSubmit{

    position: absolute;
    width: 100px;
    font-weight: bold;
    top: 20px;
    right: 50px;
    background-color: teal;
    padding: 10px;
    border: none;
    border-radius:10px;
    cursor: pointer;
    font-size: 16px;

}

