.top{

    width: 100%;
    height: 50px;
    background-color: white;
    position: sticky; /*fix element on same place**/
    top: 0;
    display: flex;
    align-items: center;
    font-family: 'Josefin Sans', sans-serif;
    z-index: 999;

}

.topLeft,.topRight{

    flex:3;
    
}

.topRight{

    right: 0;
}

.topIcon{

    
    font-size: 20px;
    margin: 20px;
    color: #444;
    cursor: pointer;
}



.topImg{

    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
    
}

.topCenter{

    flex: 6;
}

.topList{

    display: flex;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style: none;
}

.topListItem{

    margin-right: 20px;
    font-size: 18px;
    font-weight: 300;
    cursor: pointer;
    

}

.topListItem >a{

    text-decoration: none;
    color: black;
}

.topSearchIcon{

    font-size: 18px;
    color:grey;
    cursor: pointer;
}

.topRightItems{

    font-size: 20px;
    margin-right: 10px;
    
}

.postTitle{

    color: black;
}

.toprighticon{

    color: black;
}

.proflLink{

    display: flex;
    margin-right: 20px;
    justify-content: space-between;
}

.link{
    text-decoration: none;
    
    
}